import React, { Component } from 'react';

import CardList from '../../components/rolodex-components/card-list/card-list.component';
import { SearchBox } from '../../components/rolodex-components/search-box/search-box.component';

import Header from '../../components/all-reusable-components/header/header.component';

import './rolodex.styles.scss'

export class Rolodex extends Component {
  constructor(props) {
    super(props);
    this.state = {
      characters: [],
      searchField: ''
    };
  }

  changeHandle = e => {
    this.setState({
      searchField: e.target.value
    })
  }

  componentDidMount() {
    fetch('https://jsonplaceholder.typicode.com/users')
      .then(response => response.json())
      .then(users => this.setState({characters:users}))
  }

  render() {
    const { characters, searchField} = this.state;
    const filteredCharacters = characters.filter(character => (
      character.name.toLowerCase().includes(searchField.toLowerCase())
    ))
    return(
      <div className='rolodex'>
        <Header style={{color: 'white'}}/>
        <p className='blinking rolodex-title'>
        Character Rolodex
        </p>
        <SearchBox 
        placeholder="Search Characters"
        changeHandle={this.changeHandle}
        />
        <CardList characters={filteredCharacters}/>
      </div>
    )
  }
}

export default Rolodex;