import React from "react";

import QuoteBox from "../qt-box/qt-box.component";

import QUOTES_DATA from "./qt.data";

import './qt-database.styles.scss';

class QuoteData extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            quotesDB: QUOTES_DATA
        };
    }
    render() {

        const { quotesDB } = this.state;

        const random = Math.floor(Math.random()*quotesDB.length);
        const quotes = quotesDB.map(data => data.quote);
        const authors = quotesDB.map(data => data.author);
            
        const quote = quotes[random]
        const author = authors[random]     

        
        return ( 
            <div>
                <QuoteBox author={author} quote={quote} />                
            </div>     
        );
    }
};

export default QuoteData;