import React from 'react';

import { randomColor } from '../../all-reusable-components/random-color/random-color-component';

import './qt-box.styles.scss';



const QuoteBox = ({ author, quote }) => (
    <div class="qt-box-row" style={{backgroundColor: `${randomColor}`}}>
        <div className="qt-box-column qt-box-author">
            <p> -- {author}</p>
        </div>
        <div className="qt-box-column qt-box-text">
            <p>{quote}</p>
        </div>
    </div>

)

export default QuoteBox;